import * as is from "./is";

/**
 * @class Array
 */

/**
 * arguments
 * 
 * @static
 * @method argsToArray
 * @param {arguments} args arguments
 * @param {number} index 
 * @param {number} lastIndex 
 * @type {array} arguments
 */
export const argsToArray = (() => {
	let slice = Array.prototype.slice;

	return (args, index = 0, lastIndex) => {
		lastIndex = lastIndex || args.length;
		return slice.call(args, index, lastIndex);
	};
})();


/**
 * each
 * @static
 * @method each
 * @param {Array|Object|Number} obj each
 * @param {function} callback 
 * @return {object} 
 */
export function each(obj, callback) {
	let isContinue, i;

	if (is.isArray(obj)) {
		let l = obj.length;
		i = 0;
		for (; i < l; i += 1) {
			isContinue = callback.call(obj[i], obj[i], i, obj);
			if (isContinue === false) {
				break;
			}
		}

	} else if (is.isObject(obj) || is.isFunction(obj)) {
		for (i in obj) {
			isContinue = callback.call(obj[i], obj[i], i, obj);
			if (isContinue === false) {
				break;
			}
		}

	} else if (is.isNumber(obj)) {
		i = 0;
		for (; i < obj; i += 1) {
			isContinue = callback.call(null, i, i, obj);
			if (isContinue === false) {
				break;
			}
		}
	}

	return obj;
};


/**
 * 
 * @static
 * @method findMax
 * @param  {array} nums 
 * @return {number}
 */
export function findMax(nums) {
	return Math.max.apply(null, nums);
};


/**
 * 
 * @static
 * @method findMin
 * @param  {array} nums 
 * @return {number}
 */
export function findMin(nums) {
	return Math.min.apply(null, nums);
};


/**
 * 
 * @static
 * @method shuffle
 * @param {array} ary 
 * @return {array} 
 */
export function shuffle(ary) {
	return ary.sort(() => {
		return Math.random() - 0.5;
	});
};
